	module.exports = {"data":{"post":{"html":"<p>If you want to get involved in grassroot politics, you have to start small. You can join the local branch of a political party, organize petition drives, talk to your neighbors, express your views to your congressman or MP, and write letters to the editor. And maybe, just maybe, you can make a small difference. Unless your name is  Aznar, Blair, or Berlusconi, then your <a href=\"http://www.opinionjournal.com/extra/?id=110002994\">letter</a> can make a huge difference. </p>\n<p>An issue I almost always raise on my posts on foreign policy is whether Europe matters? The warbloggers say no, but constantly whine about Europe anyway. (Does that make them warwhiners?) Gerhard Schröder said no, and that made it OK for him to use the Iraq crisis for domestic purposes. The French seem to say no, except insofar it can help them reassert their influence in the world and the EU. Donald Rumsfeld's 'old Europe' remarks seemed to imply that he wished Europe would matter more. The Aznar letter is a sound reply that yes, Europe does matter. </p>\n<p>Thomas Nephew <a href=\"http://www.papascott.de/2003/01/24/2080.php#comments\">commented</a> on my small mammal post that \"[It seems] like the more important repercussions of the Iraq crisis are the transatlantic ones.\" I think so, and I think this letter is the first step to clearing things up. The transatlantic partnership is important. Stop. It is too important to abuse for domestic or EU politics. Double stop.</p>","fields":{"title":"Put It In A Letter","date":"31 Jan 2003","url":"https://papascott.de/2003-01-31-put-it-in-a-letter/"}}},"pathContext":{"slug":"/2003-01-31-put-it-in-a-letter/","prev":{"fields":{"title":"Return of Winter","date":"30 Jan 2003","slug":"/2003-01-30-return-of-winter/"}},"next":{"fields":{"title":"I Can't Tell You","date":"01 Feb 2003","slug":"/2003-02-01-i-cant-tell-you/"}}}}

